import * as React from 'react';
import * as glamorous from 'glamorous';

import { Button } from '../../atoms/Button';
import { Markdown } from '../../atoms/Markdown';

import { ConfettiMetricsBlock } from '../../molecules/ConfettiMetricsBlock';

import * as styles from './CopyWithStat-styles';

const { Div } = glamorous;

export interface ICopyWithStatProps {
  headline?: string;
  paragraph?: string;
  primaryButtonContent?: IButtonContentProps;
  secondaryButtonContent?: IButtonContentProps;
  confettiAnimation?: boolean;
  stats?: Array<IStatMetric>;
}

export interface IStatMetric {
  headline: string;
  subhead: string;
}

export interface IButtonContentProps {
  cta?: string;
  link?: string;
  color?: string;
  filled?: boolean;
  outline?: boolean;
  buttonStyle?: string;
  target?: string;
  onClick?: Function;
}

export class CopyWithStat extends React.Component<ICopyWithStatProps, void> {
  render() {
    return (
      <Div className="copy-stat-container" css={styles.container()} >
        <Div className="copy-container" css={styles.copyContainer()} >
          {
            this.props.headline &&
            <Div className="headline-wrapper" css={styles.headlineWrapper()} >
              <Markdown
                copy={this.props.headline}
                styleOverrides={styles.headlineMarkdown()}
              />
            </Div>
          }
          {
            this.props.paragraph &&
            <Div className="paragraph-wrapper" css={styles.paragraphWrapper()} >
              <Markdown
                copy={this.props.paragraph}
              />
            </Div>
          }
          {
            this.props.primaryButtonContent &&
            <Div className="button-wrapper" css={styles.buttonWrapper()} >
              <Button
                color={this.props.primaryButtonContent.color}
                filled
                isSmall
              >
                {this.props.primaryButtonContent.cta}
              </Button>
               {
                 this.props.secondaryButtonContent &&
                 <Button
                  color={this.props.secondaryButtonContent.color}
                  outline
                  isSmall
                 >
                  {this.props.secondaryButtonContent.cta}
                </Button>
               }
            </Div>
          }
        </Div>
        <Div className="confetti-conatiner" css={styles.confettiStatContainer()} >
          <ConfettiMetricsBlock
            metrics={this.props.stats}
            confettiAnimation={this.props.confettiAnimation}
          />
        </Div>
      </Div>
    );
  }
}



